import React from 'react';
import PropTypes from 'prop-types';
import { Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

const CheckoutSteps = ({ steps }) => {
	return (
		<Nav className="justify-content-center mb-4">
			{steps.map((step, index) => (
				<Nav.Item key={index}>
					{step.enabled ? (
						<LinkContainer to={step.link}>
							<Nav.Link>{step.label}</Nav.Link>
						</LinkContainer>
					) : (
						<Nav.Link disabled>{step.label}</Nav.Link>
					)}
				</Nav.Item>
			))}
		</Nav>
	);
};

// Kim tra kiu d liu vi PropTypes
CheckoutSteps.propTypes = {
	steps: PropTypes.arrayOf(
		PropTypes.shape({
			label: PropTypes.string.isRequired,
			link: PropTypes.string.isRequired,
			enabled: PropTypes.bool.isRequired,
		})
	).isRequired,
};

// D liu u vo (steps)
const stepsData = [
	{ label: 'ng Nhp', link: '/login', enabled: true },
	{ label: 'Giao Hng', link: '/shipping', enabled: true },
	{ label: 'Thanh Ton', link: '/payment', enabled: false },
	{ label: 't Hng', link: '/placeorder', enabled: false },
];

// Component chnh  s dng
const App = () => {
	return <CheckoutSteps steps={stepsData} />;
};

export default App;
