import React, { useState } from 'react';
import { Form, Button, Col, Row, Card } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import CheckoutSteps from '../components/CheckoutSteps';
import { savePaymentMethod } from '../actions/cartActions';

const PaymentScreen = ({ history }) => {
    // Ly thng tin giao hng t Redux state
    const cart = useSelector((state) => state.cart);
    const { shippingAddress } = cart;

    if (!shippingAddress) {
        history.push('/shipping');
    }

    // Phng thc thanh ton mc nh
    const [paymentMethod, setPaymentMethod] = useState('PayPal');

    const dispatch = useDispatch();

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(savePaymentMethod(paymentMethod));
        history.push('/placeorder');
    };

    return (
        <FormContainer>
            {/* Bc thanh ton */}
            <CheckoutSteps step1 step2 step3 />
            <h1 className="text-center">Phng Thc Thanh Ton</h1>

            <Card className="shadow-lg p-4 mt-4 rounded">
                <Form onSubmit={submitHandler}>
                    {/* Chn phng thc thanh ton */}
                    <Form.Group>
                        <Form.Label as="legend" className="mb-4">
                            Vui Lng Chn Phng Thc Thanh Ton
                        </Form.Label>
                        <Col>
                            {/* PayPal/Th Tn Dng */}
                            <Form.Check
                                type="radio"
                                label={
                                    <>
                                        <i className="fab fa-paypal text-primary"></i> PayPal hoc Th Tn Dng
                                    </>
                                }
                                id="PayPal"
                                name="paymentMethod"
                                value="PayPal"
                                checked={paymentMethod === 'PayPal'}
                                onChange={(e) => setPaymentMethod(e.target.value)}
                                className="mb-3"
                            />
                            {/* Momo */}
                            <Form.Check
                                type="radio"
                                label={
                                    <>
                                        <i className="fas fa-mobile-alt text-success"></i> V Momo
                                    </>
                                }
                                id="Momo"
                                name="paymentMethod"
                                value="Momo"
                                onChange={(e) => setPaymentMethod(e.target.value)}
                                className="mb-3"
                            />
                            {/* Thanh ton khi nhn hng */}
                            <Form.Check
                                type="radio"
                                label={
                                    <>
                                        <i className="fas fa-hand-holding-usd text-warning"></i> Thanh Ton Khi Nhn Hng
                                    </>
                                }
                                id="COD"
                                name="paymentMethod"
                                value="COD"
                                onChange={(e) => setPaymentMethod(e.target.value)}
                            />
                        </Col>
                    </Form.Group>

                    {/* Nt tip tc */}
                    <Row className="text-center mt-4">
                        <Col>
                            <Button type="submit" variant="success" size="lg" className="rounded">
                                Tip Tc
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </Card>
        </FormContainer>
    );
};

export default PaymentScreen;
