import React, { useState } from 'react';
import { Form, Button, Col, Row } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import CheckoutSteps from '../components/CheckoutSteps';
import { saveShippingAddress } from '../actions/cartActions';

const ShippingScreen = ({ history }) => {
    // Ly thng tin a ch giao hng t Redux state
    const cart = useSelector((state) => state.cart);
    const { shippingAddress } = cart;

    // Khi to state vi thng tin sn c (nu c)
    const [address, setAddress] = useState(shippingAddress.address || '');
    const [city, setCity] = useState(shippingAddress.city || '');
    const [postalCode, setPostalCode] = useState(shippingAddress.postalCode || '');
    const [country, setCountry] = useState(shippingAddress.country || '');

    const dispatch = useDispatch();

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(saveShippingAddress({ address, city, postalCode, country }));
        history.push('/payment');
    };

    return (
        <FormContainer>
            {/* Bc thanh ton */}
            <CheckoutSteps step1 step2 />
            <h1 className="text-center">a Ch Giao Hng</h1>

            <Form onSubmit={submitHandler} className="shadow p-4 mt-4 rounded">
                {/* a ch */}
                <Form.Group controlId="address" className="mb-3">
                    <Form.Label>a Ch</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Nhp a ch"
                        value={address}
                        required
                        onChange={(e) => setAddress(e.target.value)}
                        className="rounded"
                    />
                </Form.Group>

                {/* Thnh ph */}
                <Form.Group controlId="city" className="mb-3">
                    <Form.Label>Thnh Ph</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Nhp thnh ph"
                        value={city}
                        required
                        onChange={(e) => setCity(e.target.value)}
                        className="rounded"
                    />
                </Form.Group>

                {/* M bu in */}
                <Form.Group controlId="postalCode" className="mb-3">
                    <Form.Label>M Bu in</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Nhp m bu in"
                        value={postalCode}
                        required
                        onChange={(e) => setPostalCode(e.target.value)}
                        className="rounded"
                    />
                </Form.Group>

                {/* Quc gia */}
                <Form.Group controlId="country" className="mb-4">
                    <Form.Label>Quc Gia</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Nhp quc gia"
                        value={country}
                        required
                        onChange={(e) => setCountry(e.target.value)}
                        className="rounded"
                    />
                </Form.Group>

                {/* Nt tip tc */}
                <Row>
                    <Col className="text-center">
                        <Button type="submit" variant="success" size="lg" className="rounded">
                            Tip Tc
                        </Button>
                    </Col>
                </Row>
            </Form>
        </FormContainer>
    );
};

export default ShippingScreen;
